@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  
}
html{
  font-size: 62.5%;
}
.main{
  width: 100vw;
  height: 100vh;
  background-image: url(/weather/static/media/cover.06f860d1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-blend-mode: multiply;
  background-color: lightyellow;
}


.box{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  color: black;
  width: 25%;
  height: 75vh;
  border-radius: 2em;
  background-color:rgba(255,255,255,.2);
  background-size: cover;
  margin-top: 1em;
  box-shadow: 0 3px 6px black;
  
 
}

.inputbox{
  margin-top: 2em;
}
.inputbox input{
  border-radius: 1em;
  font-size: 1.8rem;
  
  height: auto;
  text-align: center;
  background-color: rgba(255,255,255,0.5);
 
}
input:focus{
  outline:none;
}
.location{
  margin-top: 1em;
  font-size: 2.5rem;
  text-align: center;
}

.info p{
  text-align: center;
  margin-top: 1.5em;
  font-size: 1.3rem;
  font-weight: bold;
}

.temp{
  margin-top: .51em;
  text-align: center;
  font-size: 4rem!important;
  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: 3px 3px rgba(0, 0, 0, 0.2);
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.temp .fa-thermometer-three-quarters{
  font-size: 1em;
  padding: .3em;
  color: lightcoral;
}

.temp .fa-thermometer-quarter{
  font-size: 1em;
  padding: .3em;
  color: skyblue;
}
.immg{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
 -webkit-align-items: center;
         align-items: center;
 -webkit-animation: whetercond 2s linear infinite alternate;
         animation: whetercond 2s linear infinite alternate;
 margin-top: 1em;
}
.tempin_max{
  margin-top:.3em;
  color: black;
  font-size:2rem;
}

@-webkit-keyframes drift {
  from {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
  }

  to {
   -webkit-transform: translateX(20%);
           transform: translateX(20%);

  }
}

@keyframes drift {
  from {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
  }

  to {
   -webkit-transform: translateX(20%);
           transform: translateX(20%);

  }
}
.location i{
  font-size: 3rem;
  -webkit-animation: drift 2s linear infinite alternate;
          animation: drift 2s linear infinite alternate;
}
.wheater_condition{
  text-align: center;
  margin-top: 1em;
  font-size: 2rem;
}

@-webkit-keyframes whetercond{
  from{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  to{
    -webkit-transform: scale(1.5,1.5);
            transform: scale(1.5,1.5);
    
  }
}

@keyframes whetercond{
  from{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  to{
    -webkit-transform: scale(1.5,1.5);
            transform: scale(1.5,1.5);
    
  }
}

.nodata{
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  font-size: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 3em;
 
}

.sunrise_set{
  font-size: 1.5rem!important;
  padding: 0;
  
  
}
.nodata1{
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  font-size: 2rem;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 3em;
 
}
.loading{
  width: 10rem;
  height: 10rem;
  display: -webkit-flex;
  display: flex;
  font-size: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  border-radius: 50%;
  border-top: solid red  3px;
  box-shadow: 0 2px 2px yellow;
  -webkit-animation: circle 3s linear infinite normal;
          animation: circle 3s linear infinite normal;
}

@-webkit-keyframes circle{
  from{
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes circle{
  from{
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
